<template>
  <div>
    <NavTemplent title="客服部门"></NavTemplent>
    <div style="padding: 15px 10% 15px 15px;">
      <div>
        <h5 style="font-weight: bold">数据筛选</h5>
        <el-form style="margin-top: 15px" label-width="80px" inline @submit.native.prevent size="mini">
          <el-date-picker
            @change="getDepartmentQuotaList"
            style="width: 250px"
            size="mini"
            v-model="form.date"
            type="daterange"
            format="yyyy/MM/dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
          <!--          <el-form-item label="姓名">-->
          <!--            <el-select v-model="userId">-->
          <!--              <el-option v-for="item in userList" :label="item.name" :value="item.id" :key="item.id"></el-option>-->
          <!--            </el-select>-->
          <!--          </el-form-item>-->
<!--          <el-form-item>-->
<!--            <el-button type="primary" @click="getDepartmentQuotaList">-->
<!--              <i class="el-icon-search"></i>-->
<!--              筛选</el-button>-->
<!--          </el-form-item>-->
        </el-form>
      </div>
      <div class="tableTitle">
        <span>
          <div>客服部门</div>
        </span>
      </div>
      <div>
        <div id="myChart" v-if="title.length && departmentQuotaList.length" style="width: 100%;height: 300px;padding: 15px"></div>
        <div v-else style="text-align: center;margin-top: 50px">暂无数据</div>
      </div>
    </div>
  </div>
</template>
<script>
    import moment from 'moment';
    import NavTemplent from "../../../components/NavTemplent";
    import TablePage from "../../../components/TablePage";
    export default {
        components: {
            NavTemplent,
            TablePage
        },
        data() {
            return {
                userId: '',
                userList: [],
                title: [],
                departmentQuotaList: [],
                listData: [],
                page: {
                    pageSize: 1,
                    pageNum: 50
                },
                form: {
                    date: [moment(moment().valueOf() - 7 * 24 * 3600 * 1000).format('YYYY/MM/DD'), moment(moment() + 24 * 3600 * 1000).format('YYYY/MM/DD')],
                }
            }
        },
        methods: {
            getUserList() {
                this.$post('getUserList', {
                    page: this.page.pageSize,
                    pageNum: 1000000
                }).then(res => {
                    this.userList = res.list;
                })
            },
            getDepartmentQuotaList() {
                this.$post('/getDepartmentList').then(res => {
                    this.departmentList = res.list.filter(item => {
                        return item.department_name === '客服部'
                    });
                    this.startTime = this.$root.$options.filters.dateFormat(this.form.date[0], 'YYYY/MM/DD');
                    this.lastTime = this.$root.$options.filters.dateFormat(this.form.date[1], 'YYYY/MM/DD');
                    this.$post('getDepartmentQuotaAnalytics', {
                        start_time: this.startTime,
                        end_time: this.lastTime,
                        department_id: this.departmentList[0].department_id,
                        user_id: this.userId
                    }).then(res => {
                        this.title = res.quota_list.map(item => {
                            return item.quota_name
                        });
                        this.title.unshift('name');
                        this.departmentQuotaList = res.user_quota_list;
                        if (this.title.length && this.departmentQuotaList.length) {
                            this.drawLine()
                        }
                    })
                }).catch(err => {
                    console.log(err)
                });
            },
            drawLine() {
                let myChart = this.$echarts.init(document.getElementById('myChart'));
                myChart.setOption( {
                    legend: {},
                    tooltip: {},
                    dataset: {
                        dimensions: this.title,
                        source: this.departmentQuotaList
                    },
                    xAxis: {type: 'category'},
                    yAxis: {},
                    // Declare several bar series, each will be mapped
                    // to a column of dataset.source by default.
                    series: [
                        {type: 'bar'},
                        {type: 'bar'},
                        {type: 'bar'}
                    ]
                });
            }
        },
        mounted() {
            this.getUserList();
            this.getDepartmentQuotaList();
        }
    }
</script>

<style lang="scss" scoped>
  .tableTitle{
    height: 60px;
    line-height: 60px;
    padding: 0 15px;
    background: #eee;
    margin-bottom: 15px;
    div{
      font-weight: bolder;
    }
  }
  .el-select{
    width: 100%;
  }
  .tab {
    margin: 10px;
    .active{
      background: #409EFF;
      color: #fff;
    }
    span {
      font-size: 14px;
      border-radius: 5px;
      border: 1px solid #e6e3e3;
      padding: 5px 10px;
    }
  }
</style>



